import React from 'react'

import Map from '../components/Map'
import FloorItem from '../components/FloorItem'
import CustomLink from '../components/CustomLink'
import building from '../data/building.png'
import { Container, Row, Col } from 'react-bootstrap'
import Header from '../components/Header'

const floorsData = require('../data/floors.json')

const BuildingPage = () => {
  return (
    <>
      <Container fluid className="px-5 position-fixed bg-light-gray header-breadcrumb">
        
        <Header
          tooltip={<span><strong>–û–±–µ—Ä—ñ—Ç—å –ø–æ–≤–µ—Ä—Ö</strong>, –Ω–∞—Ç–∏—Å–Ω—É–≤—à–∏ –Ω–∞ –Ω—å–æ–≥–æ –Ω–∞ –º–∞–ø—ñ –∞–±–æ —É —Å–ø–∏—Å–∫—É</span>}
          activeBreadcrumb="–§–∞–∫—É–ª—å—Ç–µ—Ç –∫–æ–º–ø‚Äô—é—Ç–µ—Ä–Ω–∏—Ö –Ω–∞—É–∫ —Ç–∞ –∫—ñ–±–µ—Ä–Ω–µ—Ç–∏–∫–∏"
        />

      </Container>

      <Container fluid className="px-5 pt-7vh">

        <Row className="my-3">
          <Col>
            <Map className="d-flex justify-content-center" asset={building} />
          </Col>
        </Row>

        <Row>
          <Col>
            <div className="floors-list">
              {
                floorsData.map(floor => (
                <CustomLink key={ floor.id } to={`/${floor.id}`} className="text-secondary">
                  <FloorItem floorNum={ floor.num } floorText={ floor.description } />
                </CustomLink>
                ))
              }
            </div>
          </Col>
        </Row>
      </Container>
    </>
  )
}

export default BuildingPage;